.wrapper {

  margin-left: 10px;
  margin-right: 10px;
}

.title {
  font-family: "Pacifista";
  position: fixed;
  top: 10px;
  z-index: 5;
  pointer-events: none;
  font-size: 8em;
  line-height: 1.1;
  margin-top: 0;
  transition: 0.7s;
  margin-left: 10px;
  width: 30vw;
}

.subtitle {
  font-family: Pacifista;
  position: fixed;
  line-height: 0;
  bottom: -30px;
  font-size: 0.7em;
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  transform: rotate(-90deg);
  -webkit-transform-origin: 0% 0%;
  -moz-transform-origin: 0% 0%;
  -ms-transform-origin: 0% 0%;
  -o-transform-origin: 0% 0%;
  transform-origin: 0% 0%;

}
#main {
  font-family: "Public Sans";
  font-size: 0.8em;
  padding-left: 0;
  padding-top:100px;
  position: fixed;
  width: 20vw;
  line-height: 1.2;
  transition: 0.6s;
  opacity: 0;


  li{
    margin-top: 5px;
    margin-bottom: 5px;

  }
  a{

    font-family: "Public Sans";
    font-weight: 500;
    transition: 0.2s;

  }
  a:hover{
    font-weight: 700;


  }
  a.active{

    //background-color: blueviolet;

  }
}



.content-article {
  margin-left: 10vw;
  margin-right: 10px;

  H1 {
    font-size: 1.9em;
    font-family: Pacifista;
  }

  H2 {
    font-size: 1.9em;
    font-family: Pacifista;
  }

  ul,
  ol,
  li{

    list-style: decimal;
  }

  em {
    font-family: "Public Sans Italic";
    display: block;
    width: 81%;
    font-weight: 100;
    font-size: 0.8em;
  }

}

.break {
  display: block;
}



.notes-images {
  position: absolute;
  right: 65vw;
  margin-right: -230px;
  width: 400px;
  opacity: 0;
  top: 0;
  display: grid;
}

.footnote {
  float: right;
  width: 200px;
  margin-right: 50px;
  clear: both;
  padding-bottom: 10px;

  p {
    margin: 0;
    font-size: 0.8em;
    line-height: 1.2;
    font-weight: 100;
  }
  strong {
    font-family: Pacifista;
    display: inline-block;
  }

  .footnote-counter {
    display: block;
    font-weight: 400;
    font-size: 0.8em;
  }

  a {
    display: inline-block;
    border-bottom: 2px solid transparent;
    //white-space: nowrap;
  }
  .footnote-backref {
    visibility:hidden;
  }

}

.pourcentage {
  position: fixed;
  top: 70px;
  text-align: right;
  z-index: 10;
  pointer-events: none;
  line-height: 1.3;
  font-size: 0.8em;
  opacity: 0;

  p {
    margin: 0;
  }
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#circle2 {
  margin-left: auto;
}

.fade {
  animation-name: fade;
  animation-duration: 5s;
}




.mobile-menu { display:none; }



